import React, { useEffect, useState } from 'react';
import { useParams,useLocation } from 'react-router-dom';
// import { decode } from 'html-entities';
import config from './config';

const PagePreview = () => {
  const { id } = useParams();
  const [pageData, setPageData] = useState(null);
  const location = useLocation();

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch(`${config.apiBaseUrl}/api/page/detail/${id}`);
        const data = await response.json();
        setPageData(data.data);
      } catch (error) {
        console.error('Error fetching page data:', error);
      }
    };
    fetchData();
    const pathname = location.pathname;
    const CustomUrl = pathname.slice(1);
    if(CustomUrl) {
      // const fetchDatas = async () => {
      // const url = `${config.apiBaseUrl}/api/page/detailwithCustomUrl`;
      // const method = 'POST';
      //   try {
      //     const response = await fetch(url, {
      //       method: method,
      //       headers: { 'Content-Type': 'application/json' },
      //       body: JSON.stringify({url:CustomUrl})
      //     });
      //     const data = await response.json();
      //     console.log("==============>",data);
      //     setPageData(data.data);
      //   } catch (error) {
      //     console.error('Error fetching page data CustomUrl:', error);
      //   }
      // }
      // fetchDatas();
    }
  }, [id,location]);

  const generateHTML = (data) => `
    <!DOCTYPE html>
    <html lang="en">
    <head>
        <meta charset="UTF-8">
        <meta name="viewport" content="width=device-width, initial-scale=1.0">
        <title>${data.title}</title>
        <meta name="description" content="${data.metaDescription}">
        <meta name="keywords" content="${data.metaKeyword}">
        <link rel="canonical" href="${data.canonicalTag}">
        <meta property="og:title" content="${data.ogTag}">

        <link href="https://stackpath.bootstrapcdn.com/bootstrap/4.5.2/css/bootstrap.min.css" rel="stylesheet">
        <style>
          body { margin: 0; padding: 0; }
          .container { padding: 20px; }
          .header { background: #f8f9fa; padding: 20px; text-align: center; }
          .header img { max-height: 50px; }
          .header .btn { margin-top: 10px; }
          .content { margin-top: 20px; }
          .footer { background: #f8f9fa; padding: 20px; text-align: center; margin-top: 20px; }
          .footer .social-icons img { width: 30px; margin: 0 10px; }
          img.banner {
            width: 80%;
            height: auto;
            display: block;
            margin: 0 auto 20px;
          }
        </style>
    </head>
    <body>
        <div class="header">
            <img src="/black-logo.png" alt="Logo">
            <h1>${data.title}</h1>
            <a href="https://officekithr.com/" class="btn btn-danger">Back to website</a>
        </div>
        ${data.imagePath ? `<img src="${data.imagePath}" alt="Banner" class="banner">` : ''}
        <div class="container content">
            ${data.pagcontent}
        </div>
        <div class="footer">
            <h5>Your Site Name</h5>
            <p>Location: Your location</p>
            <p>About Us: Brief about us text</p>
            <p>Phone: Your phone number</p>
            <div class="social-icons">
                <a href="https://www.instagram.com/yourprofile" target="_blank" rel="noopener noreferrer">
                    IG
                </a>
                <a href="https://www.facebook.com/yourprofile" target="_blank" rel="noopener noreferrer">
                    FB
                </a>
            </div>
        </div>
    </body>
    </html>
  `;

  if (!pageData) {
    return <div>Loading...</div>;
  }

  const previewHtml = generateHTML(pageData);

  return (
    <div
      dangerouslySetInnerHTML={{ __html: previewHtml }}
    />
  );
};

export default PagePreview;
